header {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  padding-bottom: 25px;
  width: 100%;

  background-color: $theme-bg;
  z-index: 10;

  @media screen and (min-width: 640px) {
    justify-content: space-between;
  }
}

.header__form {
  width: 50%;
  margin-top: 18px;

  @media screen and (min-width: 640px) {
    width: 80%;
  }
}